.card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;
}

.task-input {
    background: #FAFAFA;
    margin-bottom: 0.8rem;
    padding: 0.8rem;
    border-radius: 0.2rem;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    border: none;
    font-size: 1.6rem;
}

.button {
    width: 10.2rem;
    height: 2.9rem;
    border: none;
    text-align: center;
    font-weight: 400;
    font-size: 1.8rem;
    line-height: 2.1rem;
    cursor: pointer;
    border-radius: 0.5rem;

}

.open-add-button {
    color: #5E6C84;
}

.open-add-button:hover {
    background: #FFFFFF;
    border-radius: 0.5rem;
}

.button:hover {
    opacity: .85;
    transition: 0.3s;
}

.add-button {
    background-color: #0079BF;
    color: #FFFFFF;
    margin: 0rem 0.5rem 0.5rem 0rem;
}

.cancel-button {
    background-color: rgb(255, 99, 71);
    color: #FFFFFF;
}

@media (max-width: 1096px) {
    .button {
        width: 8.2rem;
        height: 2.5rem;
        font-size: 1.4rem;
        line-height: 2.1rem;

    }
}